import React from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";

import "./styles/font.css";
import "./styles/app.css";

import Header from "./components/Header";
import Transition from "./components/Transition";

import Home from "./screens/home/Home";
import Product from "./screens/product/Product";

export default function Router() {
    return(
        <BrowserRouter basename="/">
            <Header />

            <Switch>
                <Route exact path="/"><Home /></Route>
                <Route exact path="/produkt"><Product /></Route>

                <Route exact path="/domace-nabijacky"><Product title="Domáce nabíjačky" /></Route>
                <Route exact path="/business-nabijacky"><Product title="Business nabíjačky" /></Route>
                <Route exact path="/nabijacky-pre-elektrobicykle"><Product title="Nabíjačky pre elektrobicykle" /></Route>
            </Switch>

            <Transition />
        </BrowserRouter>
    )
}